<template>
	<van-popup v-model="visible" position="bottom" :overlay="true">
	  <van-picker 
	  :columns="columns"
	   @cancel="onCancel"
	   cancel-button-text="清除选择"
	   confirm-button-text="确认"
	   @confirm="onConfirm"
	   show-toolbar
	/>
	</van-popup>
</template>

<script>
import { Picker, Popup } from "vant";
import {getByHouse} from "@/api/v4.js"
export default{
	name: "measure-popup",
	components: {
	  [Picker.name]: Picker,
	  [Popup.name]: Popup
	},
	data(){
		return{
			visible:false,
			columns: ['生产商', '贸易商'],
			arr:[]     
		}
	},
	created() {
		getByHouse().then(res=>{
			// this.arr = res.rent
			// this.arr.unshift({"value":"","title":"全部"})
			// this.columns = res.rent.map(item=>item.title)
		})
	},
	methods:{
		show() {
		  this.visible = true;
		},
		hide() {
		  this.visible = false;
		},
		onCancel(val) {
		  this.$emit("confirm", "");
		  this.hide();
		},
		onConfirm(val,index) {
		  // let value = this.arr[index].value
		  this.$emit("confirm",val,index);
		  this.hide();
		},
	}
}	
</script>

