<template>
  <div class="my-card-page">
    <van-nav-bar
      fixed
      border
      title="我的名片"
      left-arrow
      @click-left="goBack"
    />
	<van-cell-group class="cell-group">
	  <van-cell
	    title="上传名片"
	    value="上传名片自动识别填写并获得身份认证"
	    is-link
	    value-class="long-value"
	    :border="false"
	  >
	    <van-uploader class="upload" :after-read="uploadFile" accept="image/*">
	      <span>上传名片自动识别填写并获得身份认证</span>
	    </van-uploader>
	  </van-cell>
	  <van-cell style="padding-top: 5px; padding-bottom: 15px;">
	    <van-uploader class="upload" :after-read="uploadFile" accept="image/*">
	      <div class="upload-box">
	        <van-icon
	          v-if="!form.visit_card"
	          class="icon"
	          name="plus"
	          color="#eee"
	        />
	        <img
	          v-else
	          class="uploaded-img"
	          :src="form.visit_card"
	          alt=""
	        />
	      </div>
	    </van-uploader>
	  </van-cell>
	</van-cell-group>
	<van-cell-group class="cell-group">
	  <van-field
	    v-model="form.name"
	    required
	    clearable
	    label="姓名"
	    :required="aip.name"
	    placeholder="请输入您的姓名"
	    :error-message="errors.name"
	    input-align="right"
	    error-message-align="right"
	    @input="errors.name = ''"
	  />
	  <van-field
	    v-model="form.mobile"
	    :required="aip.mobile"
	    clearable
	    label="手机号码"
	    placeholder="请输入您的手机号码"
	    :error-message="errors.mobile"
	    input-align="right"
	    error-message-align="right"
	    @input="errors.mobile = ''"
	  />
	  <van-field
	    v-model="form.position"
	    :required="aip.position"
	    clearable
	    label="职位"
	    placeholder="请输入您的职位"
	    :error-message="errors.position"
	    input-align="right"
	    error-message-align="right"
	    @input="errors.position = ''"
	  />
	  <van-field
	    v-model="form.email"
	    :required="aip.email"
	    clearable
	    label="邮箱"
	    placeholder="请输入您的邮箱"
	    :error-message="errors.email"
	    input-align="right"
	    error-message-align="right"
	    @input="errors.email = ''"
	  />
	  <van-cell title="个人简介" :border="false" />
	  <van-field
	    class="textarea"
	    :required="aip.p_profile"
	    v-model="form.p_profile"
	    type="textarea"
	    :error-message="errors.p_profile"
	    @input="errors.p_profile = ''"
	    placeholder="写点关于您的事情吧..."
	  />
	</van-cell-group>
    <van-cell-group class="cell-group">
      <van-field
        :value="selectedAreaName || userInfo.address"
        is-link
        readonly
        :required="aip.address"
        label="所在城市"
        placeholder="请选择您的地址"
        input-align="right"
        error-message-align="right"
        :error-message="errors.address"
        @click="showAreaPopup"
      />
      <van-field
        :required="aip.industry_id"
        v-model="selectedIndustry"
        is-link
        readonly
        label="行业"
        placeholder="请选择您的行业"
        input-align="right"
        error-message-align="right"
        @click="showIndustryPopup"
      />
      <van-field
        :required="aip.attrs"
        readonly
        is-link
        v-model="form.product"
        label="产品"
        placeholder="请输入您的产品"
        input-align="right"
        @click="showTextareaPopup"
      />
    </van-cell-group>

    <van-cell-group class="cell-group">
      <van-field
        v-model="form.company"
        :required="aip.company"
        clearable
        label="公司名称"
        placeholder="请输入您的公司名称"
        :error-message="errors.company"
        input-align="right"
        error-message-align="right"
        @input="errors.company = ''"
      />
	  <van-field
	    v-model="form.company_type"
	    required
	    clearable
	    label="公司类型"
	    placeholder="请选择您的公司类型"
	    :error-message="errors.company_type"
	    input-align="right"
	    error-message-align="right"
		readonly
		@click="showcompanytype"
		is-link
	  />
      <van-field
        v-model="form.c_address"
        :required="aip.c_address"
        clearable
        label="公司地址"
        placeholder="请输入您的公司地址"
        :error-message="errors.c_address"
        input-align="right"
        error-message-align="right"
        @input="errors.c_address = ''"
      />
      <van-field
        v-model="form.company_url"
        clearable
        :required="aip.company_url"
        label="公司网站"
        placeholder="请输入您的公司网站"
        :error-message="errors.company_url"
        input-align="right"
        error-message-align="right"
        @input="errors.company_url = ''"
      />
      <van-cell title="公司产品介绍" :border="false" />
      <van-field
        class="textarea"
        v-model="form.c_profile"
        type="textarea"
        :required="aip.c_profile"
        :error-message="errors.c_profile"
        @input="errors.c_profile = ''"
        placeholder="介绍一下您公司的产品吧..."
      />
    </van-cell-group>


    <van-cell-group class="cell-group">
      <van-field
        v-model="form.sms"
        center
        clearable
        label="短信验证码"
        placeholder="请输入短信验证码"
      >
        <van-button
          @click="getSms"
          :disabled="!!smsT"
          plain
          slot="button"
          size="small"
          type="danger"
          >{{ smsT ? `${smsT}s` : "获取验证码" }}
        </van-button>
      </van-field>
    </van-cell-group>

    <van-row class="cell-group">
      <van-col span="12">
        <van-button block @click="goBack">取消</van-button>
      </van-col>
      <van-col span="12">
        <van-button block type="danger" @click="handleSubmit">保存</van-button>
      </van-col>
    </van-row>

    <area-popup
      ref="areaPopup"
      :value="selectedAreaCode"
      @confirm="selectArea"
    />

    <industry-popup
      ref="industryPopup"
      :value="selectedIndustry"
      @confirm="selectIndustry"
      @clear="clearIndustry"
    />

    <textarea-popup
      ref="textareaPopup"
      :value="form.product"
      @confirm="updateProduct"
    />
	
	<company-type
	  ref="companyTypes"
	  :value="form.company_type"
	  @confirm="selecompanytype"
	/>
	
  </div>
</template>
<script>
import { mapGetters, mapState } from "vuex";
import {
  NavBar,
  Icon,
  Cell,
  CellGroup,
  Field,
  Uploader,
  Row,
  Col,
  Checkbox,
  Tag,
  Button
} from "vant";
import AreaPopup from "../components/area-popup";
import IndustryPopup from "../components/industry-popup";
import TextareaPopup from "../components/textarea-popup";
import companyType from "../components/company-type";
import { getSmsCode, checkSmsCode, register, updateUser } from "../api/base";
import { uploadImage } from "../api/v3";
import { registerInfo } from "../api/demand.js";
import { userGetBusiness } from "../api/v4";

export default {
  name: "my-card",
  components: {
    [NavBar.name]: NavBar,
    [Button.name]: Button,
    [CellGroup.name]: CellGroup,
    [Cell.name]: Cell,
    [Field.name]: Field,
    [Uploader.name]: Uploader,
    [Row.name]: Row,
    [Col.name]: Col,
    [Icon.name]: Icon,
    [Tag.name]: Tag,
    [Checkbox.name]: Checkbox,
    AreaPopup,
    IndustryPopup,
    TextareaPopup,
	companyType
  },
  data() {
    return {
      smsT: 0,
	  typeIndex:null,
      selectedArea: [],
      selectedIndustryObj: [],
      aip: {},
      form: {
        sms: "",
        name: "",
        email: "",
        mobile: "",
        company: "",
        position: "",
        p_profile: "",
        c_profile: "",
        c_address: "",
        company_url: "",
        product: "",
        visit_card: "",
		company_type:""
      },
      errors: {
        sms: "",
        name: "",
        email: "",
        mobile: "",
        company: "",
        position: "",
        p_profile: "",
        c_profile: "",
        address: "",
        industry: "",
        c_address: "",
        company_url: "",
        product: "",
		company_type:""
      }
    };
  },
  computed: {
    ...mapState(["from"]),
    ...mapGetters(["userInfo", "industries"]),
    selectedAreaCode() {
      const len = this.selectedArea.length;
      if (len > 0) {
        return this.selectedArea[len - 1].code;
      } else {
        return "100100";
      }
    },
    selectedAreaName() {
      const len = this.selectedArea.length;
      if (len > 0) {
        return this.selectedArea.map(item => item.name).join(" ");
        // return this.selectedArea[len - 1].name;
      } else {
        return "";
      }
    },
    selectedIndustry() {
      return this.selectedIndustryObj.length > 1
        ? this.selectedIndustryObj.map(item => item.name).join("/")
        : "";
    }
  },
  async mounted() {
    registerInfo().then(res => {
      for (let key in this.form) {
        res.map(item => {
          if (item.field == key) {
            this.$set(this.aip, key, !!Number(item.is_fill));
          }
        });
      }
    });
    await this.$store.dispatch("fetchUserInfo", this.userInfo.id);
    const {
      name,
      email,
      mobile,
      company,
      position,
      p_profile,
      c_profile,
      c_address,
      company_url,
      attrs,
      visit_card,
      industry_id,
      industry_names,
	  company_type
    } = this.userInfo;
    this.selectedIndustryObj = industry_names.map(item => ({
      name: item,
      id: industry_id
    }));
    this.form = {
      name: mobile ? name : "",
      email,
      mobile,
      company,
      position,
      p_profile,
      c_profile,
      c_address,
      company_url,
      visit_card,
	  company_type,
      product: attrs.length > 0 ? attrs.join("/") : ""
    };
  },
  methods: {
    // 点击返回按钮
    goBack() {
      if (this.from) {
        this.$router.back();
      } else {
        this.$router.replace("/demands");
      }
    },

    showTextareaPopup() {
      this.$refs["textareaPopup"].show();
    },

    updateProduct(val) {
      this.form.product = val;
    },
	
	showcompanytype() {
	  this.$refs["companyTypes"].show();
	},
	
	selecompanytype(val,index) {
		this.typeIndex = index+1
	   this.form.company_type = val;
	   console.log(this.form.company_type,this.typeIndex)
	},
	

    // 打开地址选择器
    showAreaPopup() {
      this.$refs["areaPopup"].show();
    },

    // 选择地址
    selectArea(val) {
      this.selectedArea = val;
      this.errors.address = "";
    },

    // 打开需求行业选择器
    showIndustryPopup() {
      this.$refs["industryPopup"].show();
    },

    // 选择需求行业
    selectIndustry(val) {
      this.selectedIndustryObj = val;
      this.errors.industry = "";
    },

    clearIndustry() {
      this.selectedIndustryObj = [];
      this.errors.industry = "";
    },

    async uploadFile({ file }) {
      const data = new FormData();
      data.append("file", file);
      const toast = this.$toast.loading({
        duration: 0,
        forbidClick: true,
        loadingType: "spinner",
        message: "上传中"
      });
      try {
        const res = await userGetBusiness(data);
        toast.clear();
        // this.form.visit_card = res.file_path;
		this.form = res 
		this.form.mobile = res.phone
		this.form.c_address = res.address
		this.form.visit_card = res.imgUrl
		
		console.log(res)
      } catch (error) {
        toast.clear();
        this.$toast.fail(error.message);
      }
    },

    async getSms() {
      if (!this.form.mobile) {
        this.$toast("请先输入手机号码");
        return;
      }
      await getSmsCode({
        uid: this.userInfo.id,
        mobile: this.form.mobile
      });
      this.smsT = 60;
      const smsid = setInterval(() => {
        this.smsT = this.smsT - 1;
        if (this.smsT == 0) {
          clearInterval(smsid);
        }
      }, 1000);
    },
    async handleSubmit() {
      const {
        sms,
        name,
        email,
        mobile,
        company,
        position,
        p_profile,
        c_profile,
        c_address,
        company_url,
        product,
        visit_card,
		company_type
      } = this.form;

      // if (!name) {
      //   this.errors.name = "请输入您的姓名";
      //   this.$toast("请输入您的姓名");
      //   return;
      // }

      // if (!mobile) {
      //   this.errors.mobile = "请输入您的手机";
      //   this.$toast("请输入您的手机");
      //   return;
      // }

      // if (!position) {
      //   this.errors.position = "请输入您的职位";
      //   this.$toast("请输入您的职位");
      //   return;
      // }

      // if (!email) {
      //   this.errors.position = "请输入您的邮箱";
      //   this.$toast("请输入您的邮箱");
      //   return;
      // }

      // if (!this.selectedArea.length && !this.userInfo.address) {
      //   this.errors.address = "请选择您的地址";
      //   this.$toast("请选择您的地址");
      //   return;
      // }

      //     if (!this.selectedIndustry) {
      // this.selectedIndustryObj[1].id =0
      //       return;
      //       this.errors.industry = "请选择您的行业";
      //       this.$toast("请选择您的行业");
      //     }

      // if (!product) {
      //   this.errors.product = "请输入您的产品";
      //   this.$toast("请输入您的产品");
      //   return;
      // }

      // if (!company) {
      //   this.errors.company = "请输入您的公司";
      //   this.$toast("请输入您的公司");
      //   return;
      // }

      if (!sms) {
        this.$toast("请输入您的短信验证码");
        return;
      }

      try {
        await checkSmsCode({
          uid: this.userInfo.id,
          vcode: sms
        });
      } catch (e) {
        console.log(e);
        this.$toast(e.message || "验证码校验失败");
        return;
      }

      const address =
        this.selectedArea.length > 0
          ? this.selectedArea.map(item => item.name).join(" ")
          : this.userInfo.address;

      try {
        if (this.userInfo.status === "0") {
          await register({
            open_id: this.userInfo.open_id,
            head_img: this.userInfo.head_img,
            name,
            email,
            mobile,
            company,
            position,
            p_profile,
            c_profile,
            c_address,
            company_url,
            address,
            attrs: product,
			company_type:this.typeIndex,
            industry_id:
              this.selectedIndustryObj.length > 0
                ? this.selectedIndustryObj[1].id
                : "",
            visit_card
          });
          setTimeout(() => {
            this.$dialog
              .alert({
                title: "温馨提示",
                message: "恭喜您注册成功",
                confirmButtonText: "确认"
              })
              .then(() => {
                this.$router.replace("/user-center");
              });
          }, 300);
        } else {
          await updateUser({
            user_id: this.userInfo.id,
            open_id: this.userInfo.open_id,
            head_img: this.userInfo.head_img,
            name,
            email,
            mobile,
            company,
            position,
            p_profile,
            c_profile,
            c_address,
            company_url,
            address,
			company_type,
            attrs: product,
			company_type:this.typeIndex,
            industry_id:
              this.selectedIndustryObj.length > 0
                ? this.selectedIndustryObj[1].id
                : "",
            visit_card
          });
          this.$dialog
            .alert({
              title: "温馨提示",
              message: "个人信息修改成功",
              confirmButtonText: "确认"
            })
            .then(() => {
              this.goBack();
            });
        }
      } catch (e) {
        console.log(e);
        this.$dialog.alert({
          title: "错误提示",
          message: e.message,
          confirmButtonText: "确认"
        });
      }
    }
  }
};
</script>

<style lang="less" scoped>
.cell-group {
  margin-top: 12px;
}

.upload-box {
  border: 1px solid #eee;
  border-radius: 5px;
  height: 50vw;
  width: 342px;
  box-sizing: border-box;
  display: flex;
  align-items: center;
  justify-content: center;
  overflow: hidden;
  text-align: center;
  .icon {
    font-size: 40px;
    display: block;
  }

  .uploaded-img {
    display: block;
    width: 100%;
    height: 100%;
	object-fit: contain;
  }
}

.long-value {
  flex: 2;
}

.product-tag {
  margin-right: 5px;
}

.btn-icons {
  font-size: 20px;
  padding: 5px;
}
</style>
