<template>
  <van-popup class="textarea-popup" v-model="visible" position="bottom">
    <div class="popup-header">
      <div class="left">
        <span class="btn" @click="clear">清除全部</span>
      </div>
      <p class="title">产品</p>
      <div class="right">
        <span class="btn" @click="onConfirm">确认</span>
      </div>
    </div>
    <p class="tips">请填写您的产品，多产品请用“/”间隔</p>
    <van-field
      class="textarea"
      v-model="keyword"
      rows="4"
      type="textarea"
      :placeholder="placeholder"
    />
  </van-popup>
</template>

<script>
import { Field, Popup, Button } from "vant";

export default {
  name: "about",
  components: {
    [Button.name]: Button,
    [Field.name]: Field,
    [Popup.name]: Popup
  },
  props: {
    confirm: Function,
    value: String,
    placeholder: {
      type: String,
      default: "请输入内容"
    }
  },
  data() {
    return {
      keyword: this.value,
      visible: false
    };
  },
  watch: {
    value(val) {
      this.keyword = val;
    }
  },
  methods: {
    show() {
      this.visible = true;
    },
    hide() {
      this.visible = false;
    },
    onConfirm() {
      this.$emit("confirm", this.keyword);
      this.hide();
    },
    clear() {
      this.$emit("confirm", "");
      this.hide();
    }
  }
};
</script>

<style lang="less" scoped>
.textarea-popup {
  .popup-header {
    display: flex;
    border-bottom: 0.5px solid #eee;

    .left,
    .right {
      width: 60px;
      .btn {
        color: rgb(84, 149, 253);
        font-size: 13px;
        padding: 15px;
        display: inline-block;
        white-space: nowrap;
      }
    }

    .left {
      text-align: left;
    }

    .right {
      text-align: right;
    }

    .title {
      flex: 1;
      font-size: 14px;
      text-align: center;
    }
  }

  .tips {
    padding: 0 15px;
    font-size: 13px;
    text-align: center;
    color: #999;
  }
}
</style>
